@keyframes rotates {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__circle {
  z-index: 10;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-bottom: 50%;
  margin-left: 50%;
  animation: 60s linear infinite rotates;
  position: absolute;
  bottom: 0;
  left: -.75vmin;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  z-index: 0;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-bottom: 50%;
  margin-left: 50%;
  animation: 3600s steps(60, end) infinite rotates;
  position: absolute;
  bottom: 0;
  left: -1.5vmin;
}

.stopwatch--speed-up-10 .stopwatch__seconds-hand {
  animation: 10s linear infinite rotates;
}

.stopwatch--speed-up-10 .stopwatch__minutes-hand {
  animation: 600s steps(60, end) infinite rotates;
}
